.card-active {
 .main-content__card-container__card {
  @include cardRotation;
 }

 div.ghost-div:nth-child(1) {
  @include tubeTop;
 }
 div.ghost-div:nth-child(2) {
  @include activeTubeMiddle;
 }
 div.ghost-div:nth-child(4) {
  @include activeTubeBottom;
 }

 .main-content__btn-container button.project-btn {
  background-color: #d83340;
  opacity: 0.7;
  color: #e9e9e9;
  border: 2px solid #eb8a9196;
  filter: drop-shadow(0 0 5px #fd6b6b80);
  box-shadow: 1px 1px 9px 0px rgba(245, 145, 145, 0.66);
  transition: opacity 300ms ease-in-out;
  &:hover {
   opacity: 1;
   cursor: pointer;
   font-weight: bold;
  }
 }
 .main-content__btn-container button:nth-child(2) {
  top: 1em !important;
 }
 .main-content__btn-container button:nth-child(3) {
  top: 2.3em !important;
 }
 .main-content__btn-container button:nth-child(4) {
  bottom: -36vh !important;
 }
 .main-content__card-sideA {
  visibility: hidden;
 }

 @include activeCardSideColor;

 aside.social-media > a > img {
  -webkit-filter: blur(0px) brightness(2.6) contrast(4.8) grayscale(0.18)
   hue-rotate(170deg) invert(0.1) opacity(100%) saturate(4.3) sepia(0.29)
   drop-shadow(0px 1px 3px #000);
  filter: blur(0px) brightness(2.5) contrast(5) grayscale(0) hue-rotate(190deg)
   invert(0) opacity(100%) saturate(6) sepia(6.29)
   drop-shadow(0px 1px 1px $activeSocialMediaColor);

  &:hover {
   -webkit-filter: blur(0px) brightness(2.7) contrast(7) grayscale(0)
    hue-rotate(190deg) invert(0) opacity(100%) saturate(6) sepia(6.29)
    drop-shadow(0px 1px 6px $activeSocialMediaColor);
   filter: blur(0px) brightness(2.7) contrast(7) grayscale(0) hue-rotate(190deg)
    invert(0) opacity(100%) saturate(6) sepia(6.29)
    drop-shadow(0px 1px 6px $activeSocialMediaColor);
  }
 }

 background-color: $activeBodyColor;

 //mobile tray animation

 .main-content__btn-container-tray__top-notch {
  animation: glow 2s ease-in-out;
  animation-delay: 1s;
 }

 @keyframes glow {
  from {
   background-color: $trayNotchBGcolor__hover;
   // background-color: palegreen;
   box-shadow: 0px 0px 5px 3px #87f1977a;
  }
  to {
   background-color: $trayNotchBGcolor__hover;
   // background-color: palegreen;
   box-shadow: 0px 0px 5px 3px #65dd777a;
  }
 }
}

.aboutMeActive {
 @include cardRotation;
}

.project-active {
 h1.main-content__card-sideB-heading {
  position: absolute;
  bottom: 5%;
  left: 32vw;
  // color: #7b2929;
 }

 div.main-content__card-container__card {
  width: 96%;
  top: 1vh;
  left: 2%;
  margin: 0 0;

  opacity: 0.6;
  cursor: pointer;
  &:hover {
   opacity: 1;
   color: #f3f3f3 !important;
  }
 }
}

.project__btn-inactive {
 .main-content__btn-container button.project-btn {
  opacity: 0.5;
 }
}

//svg arrow for mobile activation

.arrowActivate {
 display: flex !important;

 img {
  animation: dropdown 2s;
  animation-delay: calc(var(--order) * 100ms);
  //   display: none;
  opacity: 0;
 }

 @keyframes dropdown {
  from {
   opacity: 0.6;
   bottom: 0vh;
  }
  to {
   opacity: 1;
   bottom: -50vh;
  }
 }
}

.projectPromoActivateMobile {
 section.main-content__screen-container {
  display: inline-block;
 }
 .main-content__screen.project#promo-screen {
  display: flex;
 }
}

.projectTransactionActivate {
 section.main-content__screen-container {
  display: inline-block;
 }
 .main-content__screen.project#transactional-screen {
  display: flex;
 }
}

.projectNewsletterActivate {
 section.main-content__screen-container {
  display: inline-block;
 }
 .main-content__screen.project#newsletter-screen {
  display: flex;
 }
}

.projectLandingPageActivate {
 section.main-content__screen-container {
  display: inline-block;
 }
 .main-content__screen.project#landingPage-screen {
  display: flex;
 }
}

.traySlideDown {
 transform: translateY(0%);
}

